.find-id__form {
  margin-top: 40px;
  margin-bottom: 12px;
  .input.find-id__input {
    margin-bottom: 10px;
  }
  .button.find-id__button {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    margin-top: 12px;
    margin-right: 10px;
  }
}

.find-id__code {
  display: flex;
  align-items: center;
  .find-id__code-input {
    flex: 2;
    height: 40px;
    margin-right: 10px;
  }
  .find-id__code-button {
    flex: 1;
    height: 40px;
  }
}

.find-password__form {
  margin: 40px 0;
  .input.find-password__input {
    margin-bottom: 10px;
    &:nth-child(2) {
      margin-bottom: 0;
    }
  }
  .button.find-password__button {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    margin-top: 12px;
    margin-right: 10px;
  }
}
